/*
clary sage - #acad97
chesterfield green - #626439
greek villa - #F0ECE2
*/

@viewport {
	width: device-width;
	zoom: 1.0;
}

@media screen and (max-width: 415px) {
	.homepage-title {
		font-size: 20px !important;
		width: 75% !important;
		padding-left: 8vw !important;
		text-align: center;
	}
	.blog-logo-header {opacity: 0; width: auto !important; height: auto! important;}
	.nav-bar-menu {
		width: 75% !important;
	}
	.nav-bar-item {
		font-size: 14px !important;
	}
	img.blog-logo-home {
	width: 50%;
	height: 50%;
	display: block;
	}
}

html {
	background-color: #acad97;
	font-family: georgia, times-new-roman;
	overflow-y: scroll;
}

.container-flexbox {
	height: 100%;
	width: 100%;
	max-width: 100%;
	display: flex;
	flex-grow: 1;
}

.return-jwm {
	width: 33%;
	display: flex;
	font-family: georgia, times-new-roman;
	font-size: 14px;
	background-color: #acad97;
	color: #000000;
	margin-right: auto;
	border: none;
}

a.return-jwm {
	width: 60px;
	text-decoration: none;
	border: 3px solid black;
	text-align: center;
	padding: 5px;
}

.return-jwm:hover {
	color: #F0ECE2;
}

.homepage-title {
	width: 33%;
	display: flex;
	font-size: 28px;
	text-align: center;
}

.blog-logo-header {
	display: flex;
	width: 33%;
	height: 100%;
	max-width: 100%;
	margin-left: auto;
	margin-top: 0px;
}

.website-footer {
	font-family: georgia, times-new-roman;
	text-align: center;
	color: #000000;
	background-color: #acad97;
	width: 100%;
	max-width: 100%;
}

#social-media-photo {
	padding: 5px;
	width: 40px;
}

#social-media-photo:hover {
	opacity: 0.7;
}

#nav.container-flexbox {
	display: flex;
	width: 100%;
	justify-content: center;
}

.nav-bar-menu {
	width: 33%;
	max-width: 100%;
	display: flex;
}

.nav-bar-item {
	width: 33%;
	display: flex;
	font-size: 22px;
}

a.nav-bar-menu {
	color: #000000;
	text-decoration: none;
}

a.nav-bar-menu:hover {
	color: #F0ECE2;
	text-decoration: underline;
}

.current-page {
	color: #F0ECE2;
	text-decoration: underline;
}

.blog-logo-home {
	display: block;
	width: 25%;
	max-width: 100%;
	height: 25%;
	margin-left: auto;
	margin-right: auto;
}

#about.container-flexbox {
	justify-content: center;
	align-items: center;
	flex-flow: column nowrap;
}

.about-picture {
	max-width: auto;
	height: auto;
	width: 50%;
	display: block;
	justify-content: center;
	margin-left: auto;
	margin-right: auto;
}

#post.container-flexbox {
	display: flex;
	width: 100%;
	justify-content: center;
	list-style: none;
}

.post-menu {
	width: 33%;
	max-width: 100%;
	display: flex;
}

.post-menu-item {
	width: 100%;
	display: flex;
	flex-flow: column nowrap;
}

.post-menu-item-header {

}

.post-menu-item-link {
	color: #000000;
	text-decoration: none;
	justify-content: center;
}

.post-menu-item-link:hover {
	color: #F0ECE2;
	text-decoration: underline;
}

.blog-post-content {
	width: 75%;
	margin: auto;
}

.solid-port-section-divider {
	border-top: 3px solid #000000;
}

.solid-blog-section-divider {
	border-top: 1.5px solid #000000;
	width: 25%;
}